import WalletConnect from "@walletconnect/client";
import QRCodeModal from "@walletconnect/qrcode-modal";
import { convertUtf8ToHex } from "@walletconnect/utils";
//import { convertUtf8ToHex, keccak256 } from "@walletconnect/utils";
import { ethers } from "ethers";

async function test() {

const web3 = new ethers.providers.JsonRpcProvider("https://ropsten.infura.io/v3/9aa3d95b3bc440fa88ea12eaa4456161");
console.log(web3);
	
const connector = new WalletConnect({
  bridge: "https://bridge.walletconnect.org",
  qrcodeModal: QRCodeModal,
});

if (!connector.connected)
  await connector.createSession();

connector.on("connect", async (error, payload) => {
  if (error) throw error;
  const { accounts, chainId } = payload.params[0];
  console.log(accounts, chainId);  
});

connector.on("session_update", async (error, payload) => {
  if (error) throw error;
  const { accounts, chainId } = payload.params[0];
  console.log(accounts, chainId);
});

connector.on("disconnect", async (error, payload) => {
  if (error) {throw error;}
  console.log(payload);
});

const account = connector._accounts[0];

const params = new URLSearchParams(window.location.search);

const tx = {
  from: account,
  to: "0xb5829eb58303b8a53E5918A891ccAe371460159E",
  data: "0x",
  gasPrice: "0x0400000000", // "0x02540be400",
  gas: "0x9c40", // 40000
  value: "0x01",
  nonce: await web3.getTransactionCount(account), // all fields mandatory for the test wallet
};
if (params.has('signTransaction')) connector.signTransaction(tx) // not supported by metamask
  .then((result) => {console.log("signTransaction", result);})
  .catch((error) => {console.error("signTransaction", error);});

if (params.has('sendTransaction')) connector.sendTransaction(tx)
  .then((result) => {console.log("sendTransaction", result);})
  .catch((error) => {console.error("sendTransaction", error);});

const message = "Hello world";

if (params.has('signPersonalMessage')) connector.signPersonalMessage([convertUtf8ToHex(message), account])
  .then((result) => {console.log("signPersonalMessage", result);})
  .catch((error) => {console.error("signPersonalMessage", error);});

console.log(ethers.utils.keccak256(ethers.utils.toUtf8Bytes("\x19Ethereum Signed Message:\n" + message.length + message)));
console.log(ethers.utils.keccak256(convertUtf8ToHex("\x19Ethereum Signed Message:\n" + message.length + message)));
console.log(ethers.utils.id("\x19Ethereum Signed Message:\n" + message.length + message));
console.log(ethers.utils.hashMessage(message));

if (params.has('signMessage')) connector.signMessage([account, ethers.utils.hashMessage(message)])
  .then((result) => {console.log("signMessage", result);})
  .catch((error) => {console.error("signMessage", error);});

const typedData = {
  types: {
    EIP712Domain: [
      { name: "name", type: "string" },
      { name: "version", type: "string" },
      { name: "chainId", type: "uint256" },
      { name: "verifyingContract", type: "address" },
    ],
    Person: [
      { name: "name", type: "string" },
      { name: "account", type: "address" },
    ],
    Mail: [
      { name: "from", type: "Person" },
      { name: "to", type: "Person" },
      { name: "contents", type: "string" },
    ],
  },
  primaryType: "Mail",
  domain: {
    name: "Example",
    version: "1.0",
    chainId: 3,
    verifyingContract: "0x0000000000000000000000000000000000000000",
  },
  message: {
    from: {
      name: "Alice",
      account: "0xaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa",
    },
    to: {
      name: "Bob",
      account: "0xbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbb",
    },
    contents: "Hello",
  },
};
if (params.has('signTypedData')) connector.signTypedData([account, JSON.stringify(typedData)])
  .then((result) => {console.log("signTypedData", result);})
  .catch((error) => {console.error("signTypedData", error);});

const customRequest = {
  id: 1337,
  jsonrpc: "2.0",
  method: "eth_sendTransaction",
  params: [ tx ],
};
if (params.has('sendCustomRequest')) connector.sendCustomRequest(customRequest)
  .then((result) => {console.log("sendCustomRequest", result);})
  .catch((error) => {console.error("sendCustomRequest", error);});

}

test();